/**
10 -> 5 -> 16
const myLinkedList = {
  value: 10,
  next: {
    value: 5,
    next: {
      value: 16,
      next: null,
    }
  }
};
**/

type LinkedListNode = {
  value: number;
  next: LinkedListNode | null;
};

const SinglyLinkedList = (value: number) => {
  const head:LinkedListNode = {
    value,
    next: null,
  };
  let tail = head;
  let length = 1;

  function get() {
    return {
      head, tail, length,
    };
  }
  
  function append(value: number): void {
    const newNode = {
      value,
      next: null,
    };
    tail.next = newNode;
    tail = newNode;
    length++;
  };

  return {
    append,
    get,
  };
};

const myLinkedList = SinglyLinkedList(10);
myLinkedList.append(5);
myLinkedList.append(16);
console.log(myLinkedList.get());